import BorderBottom from "../../../Ui/Border/BorderBottom";
import Switch from "@mui/material/Switch";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import style from "../../InstituteStudent/InstituteStudent.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useEffect, useState } from "react";
import EditFormSection from "./FormSection/EditFormSection";
import AddField from "./AddField/AddField";
import StaffFormSettingCardMenu from "./AddField/StaffFormSettingCardMenu";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  useInstituteStaffToggleChecklist,
  useInstituteStaffUpdateSectionChecklist,
} from "../../../Hooks/ApiHooks/InstituteStaff/institute-staff-api";

const label = { inputProps: { "aria-label": "Switch demo" } };

const StaffFormSettingCard = ({ st, onRefetch, formId }) => {
  const [add, setAdd] = useState(false);
  const [addField, setAddField] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);
  const [visible, setVisible] = useState(false);
  const [disabled, setDisabled] = useState(false);
  useEffect(() => {
    setVisible(st?.section_visibilty);
  }, [st?.section_visibilty]);

  const [instituteStaffUpdateSectionChecklist] =
    useInstituteStaffUpdateSectionChecklist();

  const [instituteStaffToggleChecklist] = useInstituteStaffToggleChecklist();
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onFieldToggle = () => {
    setAddField((pre) => !pre);
  };
  const onMenuToggle = () => {
    setOpenMenu((pre) => !pre);
  };

  const onToggleChanged = (e) => {
    if (st?._id && formId) {
      setDisabled((pre) => !pre);
      instituteStaffUpdateSectionChecklist({
        formId: formId,
        bodyParameter: {
          section_name: st?.section_name,
          section_key: st?.section_key,
          section_visibilty: e.target.checked,
          fsid: st?._id,
          checklist: [],
          cid: "",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onChecklistToggleChanged = (e, checkId) => {
    if (st?._id && formId && checkId) {
      setDisabled((pre) => !pre);
      instituteStaffToggleChecklist({
        formId: formId,
        bodyParameter: {
          checkID: checkId,
          fsid: st?._id,
          form_checklist_visibility: e.target.checked,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onChecklistRequiredChanged = (checkId, flow) => {
    if (st?._id && formId && checkId) {
      setDisabled((pre) => !pre);
      instituteStaffToggleChecklist({
        formId: formId,
        bodyParameter: {
          checkID: checkId,
          fsid: st?._id,
          form_checklist_required: flow === "Yes" ? true : false,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <section className={style.sfsc_card}>
        <div className={style.sfscc_outer}>
          <div className={style.sfscc_container}>
            <Switch {...label} checked={visible} onChange={onToggleChanged} />
            <span>{st?.section_name ?? ""}</span>
          </div>
          {visible ? (
            <CreateButton
              label="edit"
              customStyle={{
                paddingBlock: "0.2rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          ) : null}
        </div>

        <div className={style.sfsccc_inner}>
          {st?.form_checklist?.map((chd) =>
            chd?.form_checklist_typo === "BUTTON" ? null : (
              <div
                className={style.sfscc_outer}
                style={{
                  width: "100%",
                  opacity: visible ? "1" : "0.7",
                  justifyContent: "flex-start",
                  gap: "1rem",
                }}
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      sx={{ color: "rgba(18, 18, 18, 0.7)" }}
                      checked={chd?.form_checklist_visibility}
                      onChange={(e) =>
                        visible ? onChecklistToggleChanged(e, chd?._id) : null
                      }
                    />
                  }
                  label={chd?.form_checklist_lable}
                  // sx={{ ...customStyleLabel }}
                  key={chd?._id}
                />

                <div>
                  (
                  <span
                    style={{
                      cursor: "pointer",
                      color: chd?.form_checklist_required
                        ? "#0942a6"
                        : "rgba(18, 18, 18, 0.7)",
                      fontWeight: chd?.form_checklist_required ? 600 : 400,
                    }}
                    onClick={() => onChecklistRequiredChanged(chd?._id, "Yes")}
                  >
                    Required
                  </span>{" "}
                  /{" "}
                  <span
                    style={{
                      cursor: "pointer",
                      color: !chd?.form_checklist_required
                        ? "#0942a6"
                        : "rgba(18, 18, 18, 0.7)",
                      fontWeight: !chd?.form_checklist_required ? 600 : 400,
                    }}
                    onClick={() => onChecklistRequiredChanged(chd?._id, "No")}
                  >
                    Optional
                  </span>
                  )
                </div>

                {/* <img
             src={`${assestsUserFinanceUrl}/menu.svg`}
             alt="menu icon"
             className={style.menu_con}
             onClick={onMenuToggle}
           /> */}
              </div>
            )
          )}
          {visible ? (
            <CreateButton
              label="add_field"
              customStyle={{
                paddingBlock: "0.2rem",
                margin: "0",
              }}
              onAction={onFieldToggle}
            />
          ) : null}
        </div>
      </section>

      <BorderBottom />
      {addField && (
        <AddField
          onClose={onFieldToggle}
          formId={formId}
          sectionId={st?._id}
          onRefetch={onRefetch}
        />
      )}
      {add && (
        <EditFormSection
          onClose={onToggle}
          formId={formId}
          sectionId={st?._id}
          onRefetch={onRefetch}
          data={st}
        />
      )}
      {disabled && <QvipleLoader />}
      {openMenu && <StaffFormSettingCardMenu onClose={onMenuToggle} />}
    </>
  );
};

export default StaffFormSettingCard;
